	module.exports = {"data":{"post":{"html":"<p>For once we are preparing our Christmas cards early... here's Mama hard at work! (And, yes, that's a Tux Linux mascot behind her, even though she's strictly a Windows woman.)</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/1999/12/fppaper.jpg\" height=\"225\" width=\"300\" border=\"0\" alt=\"fppaper.jpg: She's checking her list...\" /></p>\n<p>It seems I've caught the attention of \"Scripting News\". Sorry, Dave, but when I put Curly in my template, Mama just had to play the wav file 30 times in a row. I hope she didn't break anything! (In preparation for the oncoming onslaught of visitors from Scripting News, I'm updating the <a href=\"http://shanson.editthispage.com/faq\">FAQ</a>.)</p>\n<p>Our only big plan for the weekend is to go see the new James Bond film, which just opened in Germany, on Sunday. If we hit the early show at the one English-language theatre in Hamburg, we should have no trouble getting a seat. (Our first date 19 years ago was a Bond film, so we've always had a soft spot for the guy.)</p>","fields":{"title":"Christmas cards....","date":"11 Dec 1999","url":"https://papascott.de/1999-12-11-christmas-cards/"}}},"pathContext":{"slug":"/1999-12-11-christmas-cards/","prev":{"fields":{"title":"Friday misc...","date":"10 Dec 1999","slug":"/1999-12-10-friday-misc/"}},"next":{"fields":{"title":"Alsterhaus Christmas lights","date":"12 Dec 1999","slug":"/1999-12-12-alsterhaus-christmas-lights/"}}}}

